.tab-button {
	background-color: transparent;
	border: 2px solid #007bff;
	color: #007bff;
	font-size: 17px;
	cursor: pointer;
	transition: all 0.3s ease;
	padding: 10px;
}

.tab-button:hover {
	background-color: #007bff;
	color: #fff;
}

.tab-header {
	width: 100%;
	display: flex;
	align-items: center;
}

.tab-list {
	width: 100%;
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	grid-gap: 6px;
}
.add-more-btn {
	width: 100%;
	margin: 20px 0px 0px 0px;
}

.active {
	display: block;
}
.inactive {
	display: none;
}

.tab-content {
	border: 2px solid gray;
	padding: 10px;
	margin-top: 5px;
}
.tab-content-frontend {
	margin-top: 5px;
}
